import React from 'react';
import {CardTitle, Card, CardActions, CardMenu, IconButton, CardText, DataTable, TableHeader} from 'react-mdl';
import imageskye from '../../Images/Skye_Keyart_final.webp';

function Skye() {
    return(
        <div className="main">
            <div className='main-2'>Skye</div>
            <Card shadow={0} style={{width: '512px', margin: 'auto'}}>
                <CardTitle style={{ color: '#fff', height: '176px', margin:'auto'}}><img src={imageskye} style={{maxWidth:'80%'}} alt="iron1"/></CardTitle>
                <CardText>
                    Hailing from Australia, Skye and her band of beasts trail-blaze the way through hostile territory. With her creations hampering the enemy, and her power to heal others, the team is strongest and safest by Skye’s side.
                </CardText>
                <CardActions>
                    <DataTable className='table'
                                shadow={0}
                                rows={[
                                    {ability: 'Updraft', quantity: 0, cost: 0},
                                    {ability: 'Tailwind', quantity: 0, cost: 0},
                                    {ability: 'Cloudburst', quantity: 0, cost: 0},
                                    {ability: 'Blade Storm', quantity: 0, cost: 0}
                                ]}
                            >
                                <TableHeader name="ability">Special Abilities</TableHeader>
                                <TableHeader numeric name="quantity">Quantity</TableHeader>
                                <TableHeader numeric name="cost">Cost</TableHeader>
                            </DataTable>
                </CardActions>
                <CardMenu style={{color: '#fff'}}>
                    <IconButton name="share" />
                </CardMenu>
            </Card>
        </div>
    )
}

export default Skye;