import React from 'react';
import {CardTitle, Card, CardActions, CardMenu, IconButton, CardText, DataTable, TableHeader} from 'react-mdl';
import imagephoenix from '../../Images/Phoenix_artwork.webp';

function Phoenix() {
    return(
        <div className="main">
            <div className='main-2'>Phoenix</div>
            <Card shadow={0} style={{width: '512px', margin: 'auto'}}>
                <CardTitle style={{ color: '#fff', height: '176px', margin:'auto'}}><img src={imagephoenix} style={{maxWidth:'80%'}} alt="iron1"/></CardTitle>
                <CardText>
                    Hailing from the U.K., Phoenix's star power shines through in his fighting style, igniting the battlefield with flash and flare. Whether he's got backup or not, he'll rush into a fight on his own terms.
                </CardText>
                <CardActions>
                    <DataTable className='table'
                        shadow={0}
                            rows={[
                                {ability: 'Updraft', quantity: 0, cost: 0},
                                {ability: 'Tailwind', quantity: 0, cost: 0},
                                {ability: 'Cloudburst', quantity: 0, cost: 0},
                                {ability: 'Blade Storm', quantity: 0, cost: 0}
                            ]}
                    >
                            <TableHeader name="ability">Special Abilities</TableHeader>
                            <TableHeader numeric name="quantity">Quantity</TableHeader>
                            <TableHeader numeric name="cost">Cost</TableHeader>
                    </DataTable>
                </CardActions>
                <CardMenu style={{color: '#fff'}}>
                    <IconButton name="share" />
                </CardMenu>
            </Card>
        </div>
    )
}

export default Phoenix;