import React from 'react';
import {CardTitle, Card, CardActions, CardMenu, IconButton, CardText, DataTable, TableHeader} from 'react-mdl';
import imagesova from '../../Images/Sova_artwork.webp';

function Sova() {
    return(
        <div className="main">
            <div className='main-2'>Sova</div>
            <Card shadow={0} style={{width: '512px', margin: 'auto'}}>
                <CardTitle style={{ color: '#fff', height: '176px', margin:'auto'}}><img src={imagesova} style={{maxWidth:'80%'}} alt="iron1"/></CardTitle>
                <CardText>
                    Born from the eternal winter of Russia's tundra, Sova tracks, finds, and eliminates enemies with ruthless efficiency and precision. His custom bow and incredible scouting abilities ensure that even if you run, you cannot hide.
                </CardText>
                <CardActions>
                    <DataTable className='table'
                                shadow={0}
                                rows={[
                                    {ability: 'Updraft', quantity: 0, cost: 0},
                                    {ability: 'Tailwind', quantity: 0, cost: 0},
                                    {ability: 'Cloudburst', quantity: 0, cost: 0},
                                    {ability: 'Blade Storm', quantity: 0, cost: 0}
                                ]}
                            >
                                <TableHeader name="ability">Special Abilities</TableHeader>
                                <TableHeader numeric name="quantity">Quantity</TableHeader>
                                <TableHeader numeric name="cost">Cost</TableHeader>
                            </DataTable>
                </CardActions>
                <CardMenu style={{color: '#fff'}}>
                    <IconButton name="share" />
                </CardMenu>
            </Card>
        </div>
    )
}

export default Sova;