import React from 'react';
import {CardTitle, Card, CardActions, CardMenu, IconButton, CardText, DataTable, TableHeader} from 'react-mdl';
import imagereyna from '../../Images/Reyna_artwork.webp';

function Reyna() {
    return(
        <div className="main">
            <div className='main-2'>Reyna</div>
            <Card shadow={0} style={{width: '512px', margin: 'auto'}}>
                <CardTitle style={{ color: '#fff', height: '176px', margin:'auto'}}><img src={imagereyna} style={{maxWidth:'80%'}} alt="iron1"/></CardTitle>
                <CardText>
                    Forged in the heart of Mexico, Reyna dominates single combat, popping off with each kill she scores. Her capability is only limited by her raw skill, making her highly dependent on performance.
                </CardText>
                <CardActions>
                    <DataTable className='table'
                                shadow={0}
                                rows={[
                                    {ability: 'Updraft', quantity: 0, cost: 0},
                                    {ability: 'Tailwind', quantity: 0, cost: 0},
                                    {ability: 'Cloudburst', quantity: 0, cost: 0},
                                    {ability: 'Blade Storm', quantity: 0, cost: 0}
                                ]}
                            >
                                <TableHeader name="ability">Special Abilities</TableHeader>
                                <TableHeader numeric name="quantity">Quantity</TableHeader>
                                <TableHeader numeric name="cost">Cost</TableHeader>
                            </DataTable>
                </CardActions>
                <CardMenu style={{color: '#fff'}}>
                    <IconButton name="share" />
                </CardMenu>
            </Card>
        </div>
    )
}

export default Reyna;