import React from 'react';
import {CardTitle, Card, CardActions, CardMenu, IconButton, CardText, DataTable, TableHeader} from 'react-mdl';
import imagebrimstone from '../../Images/Brimstone_artwork.webp';

function Brimstone() {
    return(
        <div className="main">
            <div className='main-2'>Brimstone</div>
            <Card shadow={0} style={{width: '512px', margin: 'auto'}}>
                <CardTitle style={{ color: '#fff', height: '176px', margin:'auto'}}><img src={imagebrimstone} style={{maxWidth:'80%'}} alt="iron1"/></CardTitle>
                <CardText>
                    Joining from the USA, Brimstone's orbital arsenal ensures his squad always has the advantage. His ability to deliver utility precisely and from a distance make him an unmatched boots-on-the-ground commander.
                </CardText>
                <CardActions>
                    <DataTable className='table'
                                shadow={0}
                                rows={[
                                    {ability: 'Updraft', quantity: 0, cost: 0},
                                    {ability: 'Tailwind', quantity: 0, cost: 0},
                                    {ability: 'Cloudburst', quantity: 0, cost: 0},
                                    {ability: 'Blade Storm', quantity: 0, cost: 0}
                                ]}
                            >
                                <TableHeader name="ability">Special Abilities</TableHeader>
                                <TableHeader numeric name="quantity">Quantity</TableHeader>
                                <TableHeader numeric name="cost">Cost</TableHeader>
                            </DataTable>
                </CardActions>
                <CardMenu style={{color: '#fff'}}>
                    <IconButton name="share" />
                </CardMenu>
            </Card>
        </div>
    )
}

export default Brimstone;